import React, { useContext } from 'react';
import './Home.css';
import { Link } from 'react-router-dom';
import { Context } from '../..';

const Home = () => {
  const {store} = useContext(Context);
  return (
    <>
      <div className="home">
        <div className="home-mask"></div>
        <div className="home__links">
          {!store.isAuth && <Link to="login"></Link>}
          {!store.isAuth && <Link to="form"></Link>}
          <Link to="competitions"></Link>
        </div>
      </div>
    </>
  );
};

export default Home;
